<template>
    <div>
        <nav class="navbar navbar-expand-lg navbar-light bg-light position-fixed w-100"  style="z-index:999;">
            <div class="navbar-header">
                <a class="navbar-brand" href="#">
                    <img class=" rounded-circle align-items-baseline" @click="openMyPage()" style="height:2.5rem;" src="./../../../static/img/myIcon.png">
                </a>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarToggleExternalContent" aria-controls="navbarToggleExternalContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
            </div>
            <div class="collapse navbar-collapse" id="navbarToggleExternalContent">
                <ul class="navbar-nav mr-auto">
                    <li class="nav-item active" v-for="(nav, index) in navs" v-bind:key="index">
                        <a class="nav-link" href="#" @click="innerSwitchPage(nav.code)">{{nav.text}}</a>
                    </li>
                </ul>
            </div>
            <form class="form-inline">
                <input class="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search">
                <button class="btn btn-outline-success my-2 my-sm-0" type="button" @click="innerSwitchPage('search-article')">Search</button>
            </form>
        </nav>
        <my-page ref="myPage"></my-page>
    </div>
</template>
<script>
import myPage from './../myPage'
export default {
  date () {
    return { navs: [] }
  },
  components: { 'my-page': myPage },
  created () {
    this.navs = [{ text: 'Home', code: 'home-page' }]
  },
  methods: {
    innerSwitchPage (pageName) {
      this.$emit('switchPage', pageName)
    },
    openMyPage () {
      this.$refs.myPage.display()
    }
  }
}
</script>
<style>
</style>



