// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// eslint-disable-next-line
import $ from 'jquery'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import VueI18n from 'vue-i18n'
import messageZhCn from '../static/js/messages/message_zh-CN'
import messageEn from '../static/js/messages/message_en-US'
import messageJa from '../static/js/messages/message_ja-JP'
import API from '../static/js/common/API'

Vue.use(VueI18n)
Vue.use(API)
const lang = 'zh-CN'
const i18n = new VueI18n({
  local: lang,
  messages: {
    'ja-JP': messageJa,
    'en-US': messageEn,
    'zh-CN': messageZhCn
  }
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>',
  i18n
})



