<template>
    <div v-show="show" class="w-100 h-100 position-fixed" style="z-index:9999;margin-top:0px;background-color:rgba(240,240,240,0.5);">
        <button type="button" class="close p-2" aria-label="Close" @click="closeMyPage()">
            <span aria-hidden="true">&times;</span>
        </button>
        <div class="card p-3 m-5 h-100">
            <div>
                <img class=" rounded-circle align-items-baseline" style="height:2.5rem;" src="./../../static/img/myIcon.png">
                <div>
                    <span class="badge badge-secondary mr-1" v-for="(badge, index) in badges" :key="index">{{badge}}</span>
                </div>
                <div>
                    <span class=" font-weight-light form-control-plaintext" style="font-size:0.8rem;">
                        没什么想说的
                    </span>
                </div>
                <div>
                    <a class="iconfont icon-github" target="_blank" href="https://github.com/Lucouse"></a>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      show: false,
      badgs: []
    }
  },
  created () {
    this.badges = ['java', 'html', 'js', 'JQuery']
  },
  methods: {
    display () {
      this.show = true
    },
    closeMyPage () {
      this.show = false
    }
  }
}
</script>
<style>
</style>



